.skill-bar
    background-color: #f5f5f5
    height: 30px
    width: 800px
    
i
    font-size: 25px
    margin-left: 8px 

.progress-bar
  height: 100%
  

.progress-bar-color
    animation: load 3s ease 
    display: flex
    align-items: center
    height: 100%
    justify-content: space-between
    padding-right: 5px

@keyframes load 
    from 
        width: 0%
    to 
        width: 100%

@media (max-width: 768px)
  .skill-bar
    width: 500px

@media (max-width: 768px)
  .skill-bar
    width: 300px
